@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* user form */
.form-title {
	display: block;
	@include font-alt-bold;
	@include font($font-size-h2, $line-height-h2);
	color: $title-color;
	text-align: center;
	margin-bottom: $bottom-space--md; }

.user-form {
	background: $bg-color-alt;
	@include padding-vertical($text-layout-space);
	margin-bottom: $bottom-space;
	border: solid $border-color;
	border-width: 1px 0;

	&:last-child {
		margin-bottom: 0; }

	@include media('<tab-lg') {
		@include padding-vertical($text-layout-space--md); }

	@include media('<mob-lg') {
		@include padding-vertical($text-layout-space--sm); }

	form {
		display: block; }

	.input-wrap {
		margin-bottom: 50px; }

	textarea {
		height: 130px;
		max-height: 130px;
		resize: none; }

	.captcha {
		@include display-inline-block-top;
		@include clear-spacing;
		position: relative; // Важно! Для позицирования отметки подтверждения полей формы
		z-index: 1; // Важно! Отметка подтверждения полей формы имеет z-index: -1

		@include clear;

		img {
			$margin-right: 20px;
			display: inline-block;
			vertical-align: middle;
			max-width: calc(50%);
			margin-right: $margin-right;

			& + input {
				display: inline-block;
				vertical-align: middle;
				width: 200px; } }

		@include media('<tab-lg') {
			img {
				display: block;
				max-width: none;
				margin-bottom: $bottom-space--sm;

				& + input {
					display: block;
					max-width: none;
					width: 100%; } } } }

	.form-footer {
		@include clear;

		@include media('<mob-lg') {
			padding-top: $bottom-space--sm; } }

	.form-buttons {
		float: left;
		margin-right: 30px;

		@include media('<mob-lg') {
			float: none;
			margin: 0; }

		.btn-submit {
			width: 200px; } }

	.form-note {
		overflow: hidden;
		min-height: 50px;
		@include clear-spacing;
		line-height: 25px;

		&::before {
			content: '';
			display: inline-block;
			vertical-align: middle;
			min-height: inherit; } }

	.form-note__item {
		display: inline-block;
		vertical-align: middle;
		width: calc(100% - 6px);
		@include font($font-size--sm, $line-height--sm);
		letter-spacing: normal; } }

/* user form end */

/* slick sliders */
// slider
.slick-slide {
	&, & a {
		-webkit-tap-highlight-color: rgba(255, 255, 255, 0) !important;
		-webkit-focus-ring-color: rgba(255, 255, 255, 0) !important;
		outline: none !important; } }

// arrows
.slick-arrow {
	@include size(60px, 100%);
	display: block;
	position: absolute;
	border: none;
	border-radius: 0;
	background: none;
	outline: none;
	box-shadow: none;
	opacity: 1;
	z-index: 6;
	transition: {
		property: background, opacity, visibility;
		duration: 0.2s; }

	.no-touchevents &:hover {
		opacity: 1;
		background: rgba(195, 195, 195, 0.2); }

	&.slick-disabled, .no-touchevents &.slick-disabled {
		opacity: 0.25;
		-ms-filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=25);
		filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=25);
		cursor: default;

		&::before {
			opacity: 1; } }

	&::before {
		content: none; }

	&::after {
		top: 50%;
		content: "";
		position: absolute;
		opacity: 1;
		pointer-events: none;
		@include size(30px);
		transform: rotate(45deg);
		border: 0 solid #999; } }

.slick-prev {
	left: 0;

	&::after {
		left: 50%;
		border-width: 0 0 3px 3px;
		margin: -15px -10px; } }

.slick-next {
	right: 0;

	&::after {
		right: 50%;
		border-width: 3px 3px 0 0;
		margin: -15px -10px; } }

// dots
.slick-dots {
	margin: 0;
	line-height: 0;
	visibility: hidden;

	.#{$user-content} & {
		margin: 0;
		max-width: none; }

	li {
		vertical-align: top;
		width: auto;
		height: auto;
		margin: 0;

		&, .#{$user-content} & {
			padding: 5px 15px;
			margin: 0;

			&::before {
				content: none; } }

		button {
			width: 12px;
			height: 12px;
			padding: 0;
			border-radius: 0;
			background: $base-color;
			position: relative;
			transition: {
				property: background, border, box-shadow, transform;
				duration: 0.6s; }
			visibility: visible;

			&::before {
				content: '';
				position: absolute;
				left: 0;
				top: 0;
				height: 100%;
				width: 100%;
				background: #fff;
				border-radius: 0;
				opacity: 1;
				box-shadow: 0 2px 3px rgba(0, 0, 0, 0.20);
				transition: {
					property: transform, opacity;
					duration: $animation-speed; } } }

		&.slick-active button::before {
			opacity: 0; } } }

//.no-touchevents &:hover button::before
/* slick sliders end */

/* images slider */
.images-slider {
	position: relative;
	margin-bottom: $bottom-space;

	@include media('<tab-lg') {
		margin-bottom: $bottom-space--lg; } }

.images-slider__list, .slick-dotted.images-slider__list {
	padding-bottom: $bottom-space;
	margin-bottom: 0;
	position: relative;
	z-index: 2; }

.images-slider__item {
	float: none;
	display: inline-block;
	vertical-align: top;
	opacity: 0;
	white-space: normal;
	overflow: hidden;
	position: relative;
	background-color: transparent;

	.slick-initialized & {
		display: block;
		float: left;
		opacity: 1; }

	.no-js & {
		display: block;
		width: 100%;
		opacity: 1;
		margin-bottom: $bottom-space; }

	&::before {
		content: '';
		display: block;
		@include ratio(16, 9);

		.no-js & {
			content: none; } }

	img {
		position: absolute;
		@include position(0);
		text-align: center;
		max-width: none;
		width: 100%;
		height: 100%;
		@include object-fit(contain);

		.no-js &[data-lazy] {
			display: none; }

		.no-js & {
			position: relative;
			@include position(auto);
			width: auto;
			height: auto;
			@include object-fit(fill);
			margin-bottom: 1rem; } }

	.caption {
		display: none;

		.no-js & {
			display: block; } } }

.images-slider {

	.slick-arrow {
		right: 0;
		left: auto;
		top: 100%;
		transform: translate(0, 0); }

	.slick-prev {
		right: 40px; }

	// slider counter
	.slider-counter {
		position: absolute;
		left: 0;
		top: 100%;
		color: $text-color-light;

		.slide-curr {
			color: $text-color; } }

	.slick-dots, .#{$user-content} & .slick-dots {
		position: absolute;
		bottom: $bottom-space + 1;

		li {
			@include padding-horizontal(7px);

			button {
				width: 6px;
				height: 6px;
				border-radius: 50%;
				background-color: white;
				opacity: 0.5;
				transition: {
					property: opacity, transform;
					duration: $animation-speed; }

				&::before {
					content: none; } }

			&.slick-active button {
				opacity: 1; }

			.no-touchevents &:hover button {
				opacity: 1;
				transform: scale(2); } } } }

/* images slider end */

/* flashes */
.flashes {
	padding: 0 90px; }

.flashes__item, .images-slider__item .caption {
	@include font(1.4rem, 1.5);
	color: $text-color-light;

	& > *:last-child {
		margin-bottom: 0; } }

/* flashes end */

/* promo slider */
// slider
.promo-slider__list {
	// before slide load
	white-space: nowrap;
	overflow: hidden;
	// before slide load end

	&, &.slick-dotted {
		margin: 0; }

	&.slick-initialized, .no-js & {
		// after slide load
		white-space: normal;
		overflow: visible;
 } }		// after slide load end

// slide
.promo-slider__item {
	// before slide load
	float: none;
	display: inline-block;
	vertical-align: top;
	// before slide load end

	.slick-initialized & {
		// after slide load
		float: left;
		display: block;
 }		// after slide load end

	img {
		display: block;
		margin: 0 auto; } }

.promo-slider__list {

	// dots
	.slick-dots {
		bottom: 20px; } }

/* promo slider end */

/* partners slider */
// slider
.partners-slider__list {
	// before slide load
	white-space: nowrap;
	overflow: hidden;
	// before slide load end

	&, &.slick-dotted {
		margin: 0; }

	&.slick-initialized, .no-js & {
		// after slide load
		white-space: normal;
		overflow: visible;
 } }		// after slide load end

// slide
.partners-slider__item {
	// before slide load
	float: none;
	display: inline-block;
	vertical-align: top;
	// before slide load end

	.slick-initialized & {
		// after slide load
		float: left;
		display: block;
 } }		// after slide load end

.partners-slider__inner {
	text-decoration: none;
	color: inherit;
	height: 150px;
	display: flex;
	align-items: center;
	@include padding-horizontal(1rem);

	img {
		display: block;
		margin: 0 auto;
		filter: grayscale(1);
		transition: {
			property: filter;
			duration: $animation-speed; } }

	// hover
	&:hover {
		img {
			filter: grayscale(0); } } }

.partners-slider__list {

	// dots
	.slick-dots {
		bottom: 20px; } }

/* partners slider end */

/* jFiler */
.upload-file {
	opacity: 0; }

.jFiler {
	position: relative; }

.jFiler-input-dragDrop {
	margin: 0;
	cursor: pointer;
	padding: 20px;
	background: $bg-color-alt;
	border: 1px dashed $border-color;

	.no-touchevents &:hover {
		border-color: $form-border-focus-color; }

	&.dragged .jFiler-input-icon i {
		transform: translate(0, -8px); }

	.jFiler-input-icon {
		margin-top: 0;
		margin-bottom: 15px;
		text-align: center;

		i {
			display: inline-block;
			vertical-align: top;
			background: url(../../img/attach.png) no-repeat;
			background-size: contain;
			@include size(60px, 44px);
			transition: {
				property: background-color, border-color, transform;
				duration: $animation-speed; } } }

	.jFiler-input-text strong {
		display: block;
		@include font-normal;
		@include font($font-size--less, $line-height--less);
		color: $text-color;
		text-align: center; } }

.jFiler-items {
	//margin: #{$form-bottom-space} 0 0
	margin: 50px 0 0; }

.jFiler-items-list, .#{$user-content} .jFiler-items-list {
	margin: #{$bottom-space--sm} 0;

	li {
		padding-left: 0;

		&::before {
			content: none; } } }

.jFiler-item {
	margin: #{$bottom-space--sm} 0;
	@include font($font-size--sm, $line-height--sm);
	color: $text-color;
	overflow: hidden; }

.jFiler-item-icon {
	display: none; }

.jFiler-item-title {
	display: inline-block;
	vertical-align: top;
	@include font-bold;
	margin-right: 10px; }

.jFiler-item-others {
	display: inline-block;
	vertical-align: top;
	color: $text-color-light;
	position: relative;

	&::after {
		content: '';
		position: absolute;
		left: 100%;
		bottom: 0;
		@include size(999px, 0);
		border-bottom: 1px dotted $border-color;
		margin: 0 0 3px; }

	span {
		padding-right: 5px; } }

.jFiler-item-assets {
	position: absolute;
	right: 0;
	bottom: 0; }

.jFiler-item-assets ul,
.#{$user-content} ul .jFiler-item-assets ul {
	margin: 0;

	li {
		margin: 0;
		padding: 0 0 3px 8px !important;
		background: $bg-color-alt; } }

.jFiler-item-trash-action {
	display: block;
	position: relative;
	@include size(20px);
	z-index: 2;
	cursor: pointer;
	background-color: $text-color;

	&::before, &::after {
		content: '';
		position: absolute;
		left: 50%;
		top: 50%;
		@include size(16px, 2px);
		border-radius: 2px;
		background: white;
		margin: -1px 0 0 -8px; }

	&::before {
		transform: rotate(-45deg); }

	&::after {
		transform: rotate(45deg); }

	.no-touchevents &:hover::before,
	.no-touchevents &:hover::after {
		background-color: $base-color; } }

/* jFiler end */

/* breadcrumbs */
.breadcrumbs {
	color: $text-color-light;
	margin-bottom: $bottom-space--lg;

	@include media('<desk-lg') {
		margin-bottom: $bottom-space--ml; }

	&:last-child {
		margin-bottom: 0; } }

.breadcrumbs__list {
	&, .#{$user-content} & {
		padding: 0;
		margin: 0;
		list-style: none;

		li {
			padding: 0 6px 0 0;
			margin: 0;

			&::before, &:first-child::before {
				content: none; } } }

	cursor: default;
	overflow: hidden;
	@include font($font-size--sm, $line-height--sm);

	.layout-article & {
		max-width: none; }

	li {
		display: inline;
		position: relative;

		&::after {
			display: inline-block;
			vertical-align: baseline;
			content: '•';
			border: none;
			color: $base-color;
			padding-left: 10px; }

		&:last-child::after {
			content: none; } }

	a {
		padding-top: 0.5em;
		color: $text-color;
		text-decoration: none;
		cursor: pointer;

		span {
			border-bottom: 1px solid rgba(white, 0.3); }

		.no-touchevents &:hover {
			color: $base-color;

			span {
				border-color: rgba($base-color, 0.3); } } } }

/* breadcrumbs end */

/* files links */
.file {
	$file-icon-size: 42px;
	@include display-inline-block-top;
	min-height: $file-icon-size;
	padding: 0.5em;
	@include font(16px, 20px);
	color: $text-color;
	text-decoration: none;

	@include clear;

	svg {
		float: left;
		@include size($file-icon-size); }


	& > span, & > em {
		display: block;
		margin-left: #{$file-icon-size};
		padding-left: 1em; }

	& > em {
		display: block;
		font-size: 1.4rem;
		color: $text-color-light; }

	.no-touchevents &:hover {
		background-color: $base-color;

		& > em {
			color: $bg-color-base; } } }

.st-bg {
	fill: #e2e5e7; }

.st-corner {
	fill: #b0b7bd; }

.st-shadow {
	fill: #cad1d8; }

.st-text {
	fill: #fff; }

/* files links end */

/* feature list */
.feature-list {
	margin-bottom: $bottom-space--md;

	&:last-child {
		margin-bottom: 0; } }

.feature-list__item {
	margin-bottom: $bottom-space;
	@include clear;

	&:last-child {
		margin-bottom: 0; } }

.feature-list__img {
	float: left;
	width: 25%;
	margin: 0.3em $bottom-space $bottom-space--sm 0;

	@include media("<mob-lg") {
		width: auto;
		float: none;
		margin-right: 0; } }

.feature-list__content {
	overflow: hidden;

	h3, p {
		margin-bottom: $bottom-space--sm;

		&:last-child {
			margin-bottom: 0; } } }

/* feature list end */

/* ms-drop */
.ms-drop__drop-js {
	opacity: 0;
	visibility: hidden;

	.is-open & {
		opacity: 1;
		visibility: visible; } }

/* ms-drop end */

/* main-banners */
.main-banners {
	max-width: 1600px;
	margin: 0 auto; }

.main-banners__list {
	position: relative;

	&::before {
		content: "";
		display: block; } }

.main-banners__list__holder {
	position: absolute;
	left: 0;
	top: 0;
	height: 100%;
	width: 100%;
	@include clear-spacing; }

.main-banners__item {
	display: inline-block;
	vertical-align: top;
	height: 100%;
	font-size: $font-size;
	letter-spacing: normal; }

.main-banners__inner {
	display: block;
	position: relative;
	height: 100%;
	color: inherit;
	text-decoration: none;
	background-size: cover;
	background-repeat: no-repeat;
	background-position: center;
	background-color: #000;

	img {
		display: block;
		width: 100%;
		height: 100%;
		max-width: none;
		@include object-fit(cover);
		will-change: opacity;
		transition: {
			property: opacity;
			duration: $animation-speed; } }

	// hover
	&:hover {
		img {
			opacity: 0.85; } } }

.main-banners__list {
	&:nth-child(1) {
		&::before {
			@include ratio(1600, 616); }

		.main-banners__item {
			&:nth-child(1) {
				width: round-float(nth($main-banners-ratio, 1), 2); }

			&:nth-child(2) {
				width: round-float(nth($main-banners-ratio, 2), 2); } } } }

/* main-banners end */

/* new */
.new {
	position: relative;
	max-width: 1600px;
	margin: 0 auto; }

.new__list {
	position: relative;

	&::before {
		content: "";
		display: block; } }

.new__list__holder {
	position: absolute;
	left: 0;
	top: 0;
	height: 100%;
	width: calc(100% + #{$new-space});
	margin: 0 -#{round($new-space/2)};
	@include clear-spacing; }

.new__item {
	display: inline-block;
	vertical-align: top;
	height: 100%;
	padding: 0 #{round($new-space/2)} $new-space;
	font-size: $font-size;
	letter-spacing: normal; }

.new__inner {
	display: block;
	position: relative;
	height: 100%;
	color: inherit;
	text-decoration: none;
	background-size: cover;
	background-repeat: no-repeat;
	background-position: center;
	background-color: #000;

	img {
		display: block;
		width: 100%;
		height: 100%;
		max-width: none;
		@include object-fit(cover);
		will-change: opacity;
		transition: {
			property: opacity;
			duration: $animation-speed; } }

	// hover
	&:hover {
		img {
			opacity: 0.85; } } }

.new__list {
	&:nth-child(1) {
		&::before {
			@include ratio(1600, 616); }

		.new__item {
			&:nth-child(1) {
				width: round-float(nth($new-ratio, 1), 2); }

			&:nth-child(2) {
				width: round-float(nth($new-ratio, 2), 2); } } }

	&:nth-child(2) {
		&::before {
			@include ratio(1600, 496); }

		.new__item {
			&:nth-child(1) {
				width: round-float(nth($new-ratio, 3), 2); }

			&:nth-child(2) {
				width: round-float(nth($new-ratio, 4), 2); } } } }

/* new end */

/* subscribe-from */
.subscribe-from-wrap {
	display: flex;
	align-items: center; }

.subscribe-from {
	width: 456 + $grid-space*2;
	max-width: 100%;
	padding: $grid-space;
	margin: auto;

	.input-wrap {
		margin-bottom: 1rem; }

	input[type="text"], input[type="email"] {
		border: none;
		background-color: white;
		@include font-bold;
		font-size: 14px;
		line-height: 20px;
		letter-spacing: 0.1em;
		text-transform: uppercase;
		color: $text-color;
		height: 50px;
		padding: 15px 20px 15px 60px;
		box-shadow: 0 3px 7px rgba(0, 0, 0, 0.25);

		@include placeholder {
			color: #999; } }

	.input-holder {
		svg {
			display: block;
			position: absolute;
			@include size(20px);
			left: 20px;
			top: 50%;
			margin-top: -10px;
			pointer-events: none;

			path {
				fill: #999; } } }

	.form-footer {
		.btn-submit {
			display: block;
			width: 100%;
			@include font-bold;
			@include font(1.4rem, round-float(20/14, 2));
			padding: 1.5rem 1rem;
			letter-spacing: 0.1em;
			text-transform: uppercase;
			color: white;
			text-align: center;
			border: none;
			box-shadow: 0 3px 7px rgba(0, 0, 0, 0.25);
			background-color: $base-color;

			& > span {
				display: inline-block;
				position: relative; }

			&::before {
				content: "";
				display: block;
				position: absolute;
				@include size(100%);
				left: 0;
				top: 0;
				background-color: #1a1a1a;
				opacity: 0;
				will-change: opacity;
				transition: {
					property: opacity;
					duration: $animation-speed; } }

			// hover
			&:hover {
				&::before {
					opacity: 1; } } } } }

/* subscribe-from end */

/* products preview */
.products__inner {
	position: relative; }

.products__visual {
	position: relative; }

.products__figure {
	display: block;
	position: relative;
	max-width: 240px; // ограничиваем ширину изображения продукта
	@include push-auto;
	text-decoration: none;
	color: inherit;

	&:before {
		@include pseudo-block-relative();
		@include ratio(338, 389); } }

.products__img {
	@include cover-abs();

	.no-image & {
		background: url(../../img/empty.png) no-repeat center; }

	img {
		display: block;
		width: 100%;
		height: 100%;
		max-width: none;
		@include object-fit(scale-down); } }

